import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';

import { AppComponent } from './app.component';
import { IntroOverlayComponent } from './intro-overlay/intro-overlay.component';
import { LabelingDialogComponent } from './labeling-dialog/labeling-dialog.component';
import { ApiService } from './api.service';
import { QuestionRadioComponent } from './question-radio/question-radio.component';
import { QuestionTextComponent } from './question-text/question-text.component';
@NgModule({
  declarations: [
    AppComponent,
    IntroOverlayComponent,
    LabelingDialogComponent,
    QuestionRadioComponent,
    QuestionTextComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule
  ],
  providers: [
    ApiService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



