import { Component, OnInit, ViewChild } from '@angular/core';
import { ApiService } from '../api.service';
import { QuestionRadioComponent } from '../question-radio/question-radio.component';
import { QuestionTextComponent } from '../question-text/question-text.component';

interface Datapoint{
	image: String
};

interface Dataset{
  name: String,
	datapoints: Datapoint[],
};

@Component({
  selector: 'app-labeling-dialog',
  templateUrl: './labeling-dialog.component.html',
  styleUrls: ['./labeling-dialog.component.scss']
})

export class LabelingDialogComponent implements OnInit {
  @ViewChild('q1')
  q1: QuestionRadioComponent;
  @ViewChild('q2')
  q2: QuestionTextComponent;
  @ViewChild('q3')
  q3: QuestionRadioComponent;

  public redirectMessage:Boolean = false;

  constructor(private api:ApiService) { }
  private datasets:Dataset[] = [{
    name: "Healthy People",
  	datapoints: [
  		{image: 'assets/healthy1.jpg'},
  		{image: 'assets/healthy2.jpg'},
      {image: 'assets/healthy3.jpg'},
      {image: 'assets/healthy4.jpg'},
  	]
    },{
     name: "License plates",
    datapoints: [
      {image: 'assets/lp1.jpg'},
      {image: 'assets/lp2.jpg'},
      {image: 'assets/lp3.jpg'},
      {image: 'assets/lp4.jpg'},
      {image: 'assets/lp5.jpg'}
    ]},{
     name: "City Planning",
    datapoints: [
      {image: 'assets/street1.png'},
      {image: 'assets/street2.png'},
      {image: 'assets/street3.png'},
      {image: 'assets/street4.png'},
      {image: 'assets/street5.png'}
    ]}
  ];

  public question2:String = "What sort of purpose does this facade serve?";
  public answers2:String[] = ["Commercial", "Residential"];
  private currentDataset = 0;
  private currentDatapoint = 0;

  ngOnInit() {
  	this.api.getDataset(1).subscribe(
  		res => console.log(res),
  		error => console.log(error));
  }

  submitAnswer() {
  	if(!this.nextDatapoint()){
    }
  }

  skipAnswer() {
    if(!this.nextDatapoint()){
    }
  }

  skipDataset() {
    if(!this.nextDataset()){
    }
  }

  nextDatapoint() {
  	if(this.currentDatapoint >= this.datasets[this.currentDataset].datapoints.length - 1){
  		return this.nextDataset();
  	}
  	this.currentDatapoint+=1;
    if(this.currentDataset == 0) this.q1.reset();
    if(this.currentDataset == 1) this.q2.reset();
    if(this.currentDataset == 2) this.q3.reset();
    return true;
  }

  nextDataset() {
    if(this.currentDataset >= this.datasets.length - 1){
      this.redirectMessage = true;
      window.location.href = 'https://goo.gl/forms/ImjRmiTj6LNmO8oC3';
      return false;
    }
    this.currentDatapoint=0;
    this.currentDataset+=1;
    return true;
  }

}



